@charset "utf-8";
@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&family=Staatliches&family=Ubuntu&display=swap');
@import url('https://cdnjs.cloudflare.com/ajax/libs/animate.css/4.1.1/animate.min.css');

// Define defaults for each variable.

@media (max-width: 1360px) {
  .carousel {
    transform: translateX(-0.8em);
    width: 15em; 
    margin: auto;
    overflow: hidden;
  }
  #carouselswipe {
    width: 15em;
  }
  }

@media (min-width: 1360px) {
  .carousel {
    transform: translateX(-14em);
    width: 42em; 
    margin: auto;
    overflow: hidden;
  }
  #carouselswipe {
    width: 15em;
  }
}

#extitle {
  font-weight: 600;
}

.flexcolumn {
  flex: 50%;
}

.flexrow {
display: flex;
}

$base-font-family: "Roboto Condensed",BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol" !default;
$base-font-size:   16px !default;
$base-font-weight: 400 !default;
$small-font-size:  $base-font-size * 0.875 !default;
$base-line-height: 1.5 !default;

$spacing-unit:     30px !default;

$text-color:       rgb(44, 44, 44) !default;
$background-color: #ececec !default;
$brand-color:      #6385da !default;

$grey-color:       #747474 !default;
$grey-color-light: lighten($grey-color, 40%) !default;
$grey-color-dark:  darken($grey-color, 25%) !default;

$table-text-align: left !default;

// Width of the content area
$content-width:    1280px !default;

$on-palm:          720px !default;
$on-laptop:        1280px !default;

// Use media queries like this:
// @include media-query($on-palm) {
//   .wrapper {
//     padding-right: $spacing-unit / 2;
//     padding-left: $spacing-unit / 2;
//   }
// }
@mixin media-query($device) {
  @media screen and (max-width: $device) {
    @content;
  }
}

@mixin relative-font-size($ratio) {
  font-size: $base-font-size * $ratio;
}

// CUSTOM

#logo-img-header {
  height: 2em;
  transition: ease-in-out;
  transition-duration: 0.2s;
}

#title-on-header {
  transition: ease-in-out;
  transition-duration: 0.2s;
}

#title-on-header:hover {
  transform: scale(1.1);
}

.wrapper > a {
  color: white;
  text-decoration: none;
}

.wrapper > a:hover {
  color: white;
  text-decoration: none;
}

.page-link {
  color: white;
  text-decoration: none;
  transition: ease-in-out;
  transition-duration: 0.1s;
}

.page-link:hover {
  font-weight: bold;
  color: white;
  text-decoration: none;
  transform: scale(1.2);
}

ul > li > h3 > a {
  transition: ease-in-out;
  transition-duration: 0.1s;
  
}

ul > li > h3 > a:hover {
  transition: ease-in-out;
  font-size: 0.9em;
  text-decoration: none;
  filter: brightness(0.5);
}

ul > li > h3 > a:visited {
  font-size: 1em;
  text-decoration: none;
  filter: brightness(1);
}

#acerca-de-container {
  width: 100vw;
}
.page-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: 0;
  overflow: hidden;
  padding: 0;
}


#page-content {
  margin: 0;
  padding: 0;
  width: 100vw;
  overflow: hidden;
}

.page-content > .wrapper {
  overflow: hidden;
  display: flex;
  width: 100vw;
  padding: 0;
  margin: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  text-justify: left;
  
}

.post-content > .container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  
}

#acerca-de-container > div {
  text-justify: left;
  padding: 1em;
  margin: 0.1em 1em 0.1em 0em;
}

.carousel-item > img {
  width: 100%;
}

button {
  transition: ease-in-out;
  animation-duration: 1s;
}

button:hover {
  transform: scale(1.1);
}

.post {
  font-size: 1.5em;
}


